@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro:ital,wght@0,200;0,400;0,700;0,900;1,200;1,400&display=swap);
:root {
    --main-color: #5D5D99;
    --main-background-color: #E8E8F1;
    --dark-text-color: #353557;
    --card-background-color: #F3F3F8;
    --card-shadow-default: #6a6a8bea;
    --card-shadow-on-hover: #A1A1C5;
    --card-detail-color: var(--main-color);
    --main-font-size: 3.5em;
    --secondary-font-size: 1.6em;
    --small-font-size: 1em;
    --btn-font-weight: 900;
    --main-content-padding: 0px;
    --badge-max-width: 20vw;
    --badge-max-height: 20vw;
}

::selection {
    color: #F3F3F8;
    color: var(--card-background-color);
    background: #5D5D99;
    background: var(--main-color);
}

* {
    font-family: 'Source Code Pro', monospace;
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    border: 0;
}

body {
    min-width: 320px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-content: space-around;
    background-color: #E8E8F1;
    background-color: var(--main-background-color)
}

a, a:visited, a:hover, a:active {
    color: inherit;
    text-decoration: none;
    cursor: default;
}

h1, h2, h3, h4, h5, h6 {
    font-size: 3.5em;
    font-size: var(--main-font-size);
    color: #5D5D99;
    color: var(--main-color);
}

p {
    font-size: 1.6em;
    font-size: var(--secondary-font-size);
    text-align: justify;
}

svg {
    stroke-linejoin: round;
}

#root {
    display: flex;
    flex-direction: column;
}

#root main {
    padding: 0px;
    padding: var(--main-content-padding);
    min-width: 60vw;
    max-width: 60vw;
    align-self: center;
}

ul {
    list-style-type: none;
}

@media screen and (max-width: 1950px) {
    #root main {
        min-width: 80vw;
        max-width: 80vw;
    }
}

@media screen and (max-width: 1600px) {
    :root {
        --main-content-padding: 0px 50px 0px 50px;
    }
    #root main {
        min-width: 100%;
        max-width: inherit;
    }
}

@media screen and (max-width: 1000px) {
    :root {
        --main-content-padding: 0px 25px 0px 25px;
        --main-font-size: 3em;
        --secondary-font-size: 1.4em;
        --small-font-size: 1em;
    }
}

@media screen and (max-width: 500px) {
    :root {
        --main-content-padding: 0px 3px 0px 3px;
        --main-font-size: 2em;
        --secondary-font-size: 1.2em;
    }
}
h1.aboutMePageHeader {
    font-weight: bold;
    padding: 10px 0px 10px 0px;
}

.aboutMePageContent {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding-bottom: 20px;
}

.aboutText .aboutMePageContent {
    padding-bottom: 30px;
}

.technologies .aboutMePageContent>div {
    display: flex;
    flex-direction: column;
    min-width: 25%;
    max-width: 25%;
}

.aboutMePageContent .stackInfoCard li{
    color: var(--main-color);
    font-size: var(--secondary-font-size);
}

.technologies .aboutMePageContent .left {
    padding: 0px 20px 0px 0px;
}

.technologies .aboutMePageContent .right {
    padding: 0px 0px 0px 20px;
}

.technologies .aboutMePageContent .centerLeft {
    padding: 0px 10px 0px 0px;
}

.technologies .aboutMePageContent .centerRight {
    padding: 0px 0px 0px 10px;
}

.aboutMePageContent .card {
    margin-bottom: 10px;
    height: -webkit-fill-available;
}

@media screen and (max-width: 1000px) {
    .technologies .aboutMePageContent>div {
        min-width: 50%;
        max-width: 50%;
    }
    .technologies .aboutMePageContent .left {
        padding: 0px 5px 0px 0px;
        order: 3;
    }
    .technologies .aboutMePageContent .right {
        padding: 0px 0px 0px 5px;
        order: 4;
    }
    .technologies .aboutMePageContent .centerLeft {
        padding: 0px 5px 0px 0px;
        order: 1;
    }
    .technologies .aboutMePageContent .centerRight {
        padding: 0px 0px 0px 5px;
        order: 2;
    }
}

@media screen and (max-width: 500px) {
    .technologies .aboutMePageContent>div {
        min-width: 100%;
    }
    .technologies .aboutMePageContent .left {
        padding: 0px;
    }
    .technologies .aboutMePageContent .right {
        padding: 0px;
    }
    .technologies .aboutMePageContent .centerLeft {
        padding: 0px;
    }
    .technologies .aboutMePageContent .centerRight {
        padding: 0px;
    }
}
.card {
    border-top: 3px solid var(--card-detail-color);
    padding: 20px;
    box-shadow: 0 0 5px var(--card-shadow-default);
    transition: box-shadow .4s;
    background-color: var(--card-background-color);
    border-radius: 10px;
    color: var(--dark-text-color);
}

.card:hover {
    box-shadow: 0 0 11px var(--card-shadow-on-hover);
}

.card :is(h1, h2, h3, h4, h5, h6) {
    font-size: var(--secondary-font-size);
    font-weight: 900;
    padding: 5px 10px 5px 10px;
}

.card p {
    font-size: var(--secondary-font-size);    
    padding: 5px 15px 10px 15px;
}

.card li {
    font-size: var(--secondary-font-size);
}
.badge {
    max-width: var(--badge-max-width);
    max-height: var(--badge-max-height);
    padding: 10px;
    display: flex;
    justify-content: center;
    align-content: center;
    align-items: center;
}

.badge * {
    max-width: var(--badge-max-width);
    max-height: var(--badge-max-height);
}
.stackInfoCardContent {
    padding-bottom: 20px;
}

.stackInfoCard svg {
    fill: var(--main-color);
}

.stackInfoCard svg *{
    fill: var(--main-color);
}

.stackInfoCardContent h1 {
    text-align: center;
    padding: 5px;
}

.stackInfoBadge {
    padding: 20px 0px 15px 0px;
    display: flex;
    justify-content: center;
}

.stackInfoBadge .badge {
    width: 80%;
}

.stackInfoSmallBadge {
    padding: 20px 0px 15px 0px;
    display: flex;
    justify-content: center;
}

.stackInfoSmallBadge .badge {
    width: 30%;
}

.stackInfoCardContent ul {
    padding: 10px;
}

.stackInfoCardContent li {
    text-align: center;
    padding: 5px;
}

@media screen and (max-width: 500px) {
    .stackInfoCard {
        --badge-max-width: 50vw;
    }
}
.button {
    border-radius: 5px;
    display: flex;
    transition: -webkit-filter .2s;
    transition: filter .2s;
    transition: filter .2s, -webkit-filter .2s;
    justify-content: space-around;
}

.button:hover {
    -webkit-filter: brightness(92%);
            filter: brightness(92%);
}

.button:active {
    -webkit-filter: contrast(70%);
            filter: contrast(70%);
}

.button .buttonIcon {
    display: flex;
}



.button svg {
    min-width: 32px;
    min-height: 32px;
    max-width: 64px;
    max-height: 64px;
}
.button.headerButton {
    padding: 10px 30px 10px 30px;
    margin: 5px;
    border: 1px solid var(--main-color);
    background-color: var(--card-background-color);
    color: var(--main-color);
    font-weight: var(--btn-font-weight);
    font-size: var(--secondary-font-size);
}

.button.headerButton:hover {
    -webkit-filter: saturate(500%) drop-shadow(0 0 1px var(--main-color));
            filter: saturate(500%) drop-shadow(0 0 1px var(--main-color));
}

.button.headerButton:active {
    -webkit-filter: saturate(1500%) drop-shadow(0 0 3px var(--main-color));
            filter: saturate(1500%) drop-shadow(0 0 3px var(--main-color));
}

.button.headerButton>* {
    align-self: center;
}
.headerMenu {
    display: flex;
    justify-content: center;
    padding: var(--main-content-padding);
    padding-top: 2px;
    padding-bottom: 2px;
    background-color: var(--card-background-color);
    box-shadow: 0 0 3px var(--card-shadow-default);
}

.headerMenuContainer {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    min-width: 80vw;
}

.headerMenuContainer .socialIcons {
    display: flex;
    align-items: center;
    justify-items: center;
    justify-content: center;
    margin: 5px;
    margin-left: auto;
}

.headerMenuContainer .socialIcons .headerSocialButton {
    padding: 10px;
    height: 100%;
    background-color: var(--card-background-color);
}


.headerMenuContainer .socialIcons svg {
    fill: var(--main-color);
}

.headerMenuContainer .socialIcons svg * {
    fill: var(--main-color);
}

@media screen and (max-width: 1600px) {
    .headerMenuContainer {
        min-width: 100%;
    }
    .headerMenu {
        justify-content: flex-start;
    }
}

@media screen and (max-width: 750px) {
    .headerMenuContainer {
        flex-direction: column;
    }
    .headerMenuContainer .socialIcons {
        margin-left: 5px;
    }
}
.homePageContent {
    margin-top: 25px;
    margin-bottom: 25px;
}

.homePageContent h1 {
    text-align: center;
    margin-bottom: 15px;
}

.favoriteProjectsList {
    display: flex;
    justify-content: space-around;
}

.favoriteProjectsList>li {
    padding: 15px 0px;
}

.favoriteProjectsList>li:nth-child(2) {
    min-width: 40%;
    padding: 0px 15px;
}

footer {
    margin: 25px auto;
    max-width: 65%;
}

footer .socialButtons {
    display: flex;
}

footer .socialButtons .button {
    width: 100%;
}

footer .socialButtons .button~.button {
    margin-left: 10px;
}
.welcomeSection {
    display: flex;
    align-items: center;
    flex-direction: column;
    margin-bottom: 25px;
}

.welcomeSection .consoleStyleText {
    color: var(--main-color);
    font-size: calc(1*var(--main-font-size));
    font-weight: bold;
    padding: 1em;
    border-radius: 10px;
}

.welcomeSection .profilePic {
    max-width: 25%;
    padding: 12px;
    box-shadow: 0 0 3px var(--card-shadow-on-hover);
    -webkit-filter: drop-shadow(10px 10px 10px var(--card-shadow-on-hover));
            filter: drop-shadow(10px 10px 10px var(--card-shadow-on-hover));
    align-items: center;
    justify-content: center;
    align-content: center;
    background-color: var(--card-background-color);
    border-radius: 12px;
    display: flex;
}

.welcomeSection .profilePic * {
    min-width: 100%;
    min-width: 100%;
    border-radius: 10px;
    max-width: 100%;
    max-height: 100%;
}

@media screen and (min-width: 1850px) {
    .welcomeSection .consoleStyleText {
        font-size: calc(1.2*var(--main-font-size));
    }
}

@media screen and (max-width: 1000px) {
    .welcomeSection {
        flex-direction: column;
        align-items: center;
    }
    .welcomeSection .profilePic {
        max-width: 30vw;
        margin-bottom: 10px;
    }
}
.card.stackBadgeCard {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    font-size: calc(0.6 * var(--secondary-font-size));
    font-weight: bold;
}

.card.stackBadgeCard .badge {
    margin-bottom: 10px;
    padding: 20px;
}

.card.stackBadgeCard .stackBadgeCardLabel {
    color: var(--main-color);
    margin-bottom: 15px;
}

.card.stackBadgeCard svg {
    fill: var(--main-color);
    min-width: 8px;
    min-height: 8px;
    width: 60%;
}

.card.stackBadgeCard svg * {
    min-width: 8px;
    min-height: 8px;
}

@media screen and (max-width: 500px) {
    .stackSection .mainStackCards ul {
        align-items: center;
        width: 90%;
    }
}

@media screen and (max-width: 750px) {    
    .stackSection .mainStackCards ul li {
        min-width: 25%;
        max-width: 25%;
    }
    .card.stackBadgeCard {
        padding: 5px;
        font-size: calc(0.5 * var(--secondary-font-size));
    }
    .card.stackBadgeCard .badge {
        margin-bottom: 5px;
        padding: 10px;
    }
    .card.stackBadgeCard .stackBadgeCardLabel {
        margin-bottom: 5px;
    }
}
.stackSection {
    transition: opacity 0.6s linear;
}

.stackSection .mainStackCards {
    display: flex;
    flex-direction: column;
}

.stackSection .mainStackCards ul {
    justify-content: center;
    min-width: -webkit-min-content;
    min-width: min-content;
    width: 100%;
    display: flex;
}

.stackSection .mainStackCards ul li {
    min-width: 18%;
    max-width: 18%;
    display: flex;
    margin: 10px;
}

.stackSection .mainStackCards ul li>* {
    width: 100%;
}

@media screen and (max-width: 750px) {
    .stackSection .mainStackCards ul li {
        min-width: 24%;
        max-width: 25%;
    }
}
.button.cardButton {
    text-align: center;
    background-color: var(--main-background-color);
    display: flex;
    padding: 15px 25px 15px 25px;
    margin-top: 10px;
}

.button.cardButton>* {
    color: var(--main-color);
    font-weight: var(--btn-font-weight);
    align-self: center;
}

.button.cardButton .buttonIcon {
    margin-left: auto;
}

.button.cardButton svg {
    min-height: 16px;
    max-height: 56px;
    fill: var(--main-color);
}

.button.cardButton svg * {
    fill: var(--main-color);
}

@media screen and (max-width: 750px) {
    .button.cardButton {
        padding: 10px 15px 10px 15px;
    }
}
.projectInfoCard {
    height: 100%;
    width: 100%;
}

.projectInfoCardContent {
    display: flex;
    flex-direction: column;
    height: 100%;
}

.projectInfoCardContent>*:nth-last-child(1) {
    margin-top: 5px;
}

.projectInfoCardContent h1 {
    display: flex;
    min-height: calc(3 * var(--secondary-font-size));
    text-align: center;
    margin-bottom: 0.5em;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    align-content: center;
}

.projectInfoCardContent p {
    font-size: calc(0.8*var(--secondary-font-size));
    text-align: center;
    padding: 0px 7%;
    margin-bottom: 20px;
}

.projectInfoCardContent .projectTechIcon svg {
    min-width: 32px;
    min-height: 32px;
}

.projectInfoCardContent .projectTechIcon svg {
    fill: var(--main-color);
}

.projectInfoCardContent .projectTechIcon svg * {
    fill: var(--main-color);
}

.projectInfoCardContent .projectTechIcon {
    display: flex;
    flex-wrap: nowrap;
    flex-direction: column;
}

.projectInfoCardContent ul.techIconsList {
    display: flex;
    margin-top: auto;
    margin-bottom: auto;
    justify-content: center;
    padding: 0px 10px;
    flex-wrap: wrap;
}

.projectInfoCardContent ul.techIconsList li {
    margin: 10px;
    display: flex;
}
.projectsPageContent {
    --project-margin-size: 10px;
    --project-margin-correction: -10px;
}

.projectsPageContent .projectCardsGrid {
    margin-left: var(--project-margin-correction);
    margin-right: var(--project-margin-correction);
    display: flex;
    flex-wrap: wrap;
}

.projectsPageContent ul.projectCardsGrid > li {
    padding: var(--project-margin-size);
    max-width: 25%;
}

@media screen and (max-width: 1600px) {
    .projectsPageContent ul.projectCardsGrid > li {
        max-width: 33%;
    }
}

@media screen and (max-width: 1000px) {
    .projectsPageContent ul.projectCardsGrid > li {
        max-width: 50%;
    }
}

@media screen and (max-width: 750px) {
    .projectsPageContent ul.projectCardsGrid > li {
        max-width: 100%;
    }
}
.workResumeInfoCard h2 {
    font-size: var(--secondary-font-size);
    color: var(--main-color);
}

.workResumeInfoCard .subtitle {
    font: optional;
    padding-left: 15px;
    font-style: italic;
}

.workResumeInfoCard li {
    margin: 10px;
    text-align: center;
}
.workResumePage section:first-child {
    margin-top: 20px;
}

.workResumePage section:last-child {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.workResumePage section:last-child>h1 {
    min-width: 100%;
}

.workResumePage section:last-child>.workResumeInfoCard {
    display: flex;
    flex-wrap: wrap;
    min-width: 49%;
    max-width: 49%;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 10px;
}

.workResumePage section:last-child>.workResumeInfoCard h2 {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    min-height: calc(2 * var(--secondary-font-size));
    margin-bottom: 10px;
}

.workResumePage h1 {
    font-weight: bold;
}

.workResumePage section {
    margin-bottom: 20px;
}

.workResumeCard {
    display: flex;
    justify-content: space-between;
}

.workResumeQuote {
    padding: 25px;
}

.workResumeQuote blockquote {
    font-size: var(--main-font-size);
    color: var(--main-color);
    white-space: pre-wrap;
}

.workResumeQuote>* {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}

.workResumeQuote figcaption {
    font-size: var(--secondary-font-size);
    font-weight: bold;
    color: var(--main-color);
    padding-top: 20px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

.workSkills .workSkillsCard {
    font-weight: bold;
}

.workSkills .workSkillsCard ul {
    padding-left: 15px;
    padding-right: 15px;
}

.workSkills .workSkillsCard li {
    margin: 10px;
    text-align: center;
}

@media screen and (max-width: 1000px) {
    .workResumePage section:last-child>.workResumeInfoCard {
        flex-direction: column;
        align-items: stretch;
        min-width: 100%;
        max-width: 100%;
    }
}

@media screen and (max-width: 500px) {
    .workResumeCard {
        flex-direction: column;
    }
}
.workResumeTitle .workResumeCard>* {
    margin: 20px;
}

.workResumeTitle .personName {
    font-size: var(--main-font-size);
    font-weight: bold;
    color: var(--main-color);
}

.workResumeTitle .personRole {
    padding-left: 5%;
    color: var(--main-color);
    font-weight: bold;
    font-size: var(--small-font-size);
}

.workResumeTitle .personAddress {
    font-size: var(--small-font-size);
    padding-left: 5%;
}

.workResumeTitle .profilePic {
    width: 35%;
    max-width: 400px;
    display: flex;
    height: -webkit-min-content;
    height: min-content;
    align-self: center;
    padding: 1px;
    border-radius: 100%;
