}

.workResumeTitle .content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 65%;
}

.workResumeTitle .personSocial .cardButton {
    display: flex;
    justify-content: space-between;
}

.workResumeTitle .personSocial li {
    padding: 2px;
}

.workResumeTitle .profilePic * {
    width: 100%;
    height: 100%;
    border-radius: 100%;
}

@media screen and (max-width: 1000px) {
    .workResumeTitle .profilePic {
        width: 40%;
        min-width: 200px;
    }
    .workResumeTitle .content {
        width: 60%;
    }
}

@media screen and (max-width: 750px) {
    .workResumeTitle .workResumeCard{
        flex-direction: column;
    }
    .workResumeTitle .profilePic {
        width: 90%;
